import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { LoginSocialGoogle, LoginSocialFacebook, LoginSocialLinkedin, LoginSocialTwitter } from 'reactjs-social-login';
import { FacebookLoginButton, GoogleLoginButton, LinkedInLoginButton, TwitterLoginButton, } from 'react-social-login-buttons';
import { userSignUp, loginUser, getIPDetails } from '../../../actions/loginActions'
import { SocialSignup } from "./socialSignup/SocialSignup";
import { BASE_URL, REACT_LOGIN } from "../../../actions/api";
import { GET_LOCAL, getCookieValue } from "../../../utils";
import { Cookies } from "react-cookie";
import { GET_GOOGLE_DATA, GOOGLE } from "../../../actions/socialUrl";


const socialLogin = new BroadcastChannel('socialLogin');
const cookies = new Cookies();
class Login extends Component {
    constructor(props) { super(props); this.controller = new AbortController(); }
    state = { accountEmail: null, accountPassword: null, emailError: null, passwordError: null, loginLoader: false, loginSuccessError: null, memberRemember: true }


    handleMessage = async (event) => {
        console.log(event)
        const { error, code } = event.data;
        if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
            if (code) {
                try {
                    const { data: { access_token } } = await GOOGLE(code);
                    const { data } = await GET_GOOGLE_DATA(access_token);
                    var picture = data.picture ? data.picture : "";
                    const obj = { accountFullName: data.name, accountEmailId: data.email, createFrom: 'google', picture, authId: data.sub }
                    this.socialLogin(obj)

                } catch (error) {
                    
                }
            }
        }
    };

    componentDidMount() {
        //  if(!REACT_LOGIN) window.location.href = `${BASE_URL}/accounts/logout` 
        // socialLogin.addEventListener("message",(e) => {
        //     console.log('event', e)
        // })

        const signal = this.controller.signal;
        window.addEventListener("message", this.handleMessage, { signal });


        // socialLogin.addEventListener("message",this.readAuthentication)
        // const email = cookies.get('member_login') ? cookies.get('member_login') : null
        // const pass = cookies.get('member_password') ? cookies.get('member_password') : null
        // this.setState({accountEmail: email, accountPassword: pass})

    }
    componentWillUnmount(){
        this.controller.abort()
        // socialLogin.removeEventListener("message",this.readAuthentication);
    }

    updateMemberData = () => {
        const { accountEmail, accountPassword } = this.state;
        cookies.set('member_login', accountEmail, { path: '/', expires: new Date(Date.now() + (30 * 24 * 60 * 60)) })
        cookies.set('member_password', accountPassword, { path: '/', expires: new Date(Date.now() + (30 * 24 * 60 * 60)) }) 
    }

    readAuthentication =(event)=>{
        console.log('event',event)
        const response = JSON.parse(event.data);
        this.socialLogin(response)

    }

    emailTextChange = event => { this.setState({ accountEmail: event.target.value, emailError: null })}
    passwordTextChange = event => { this.setState({ accountPassword: event.target.value, passwordError: null })}
    showPass = event => {
        var x = document.getElementById("AccountPassword");

        var element = document.getElementById("show_password_icon");
        element.classList.toggle("view_pass");

        if (x.type === "password") { x.type = "text"; } 
        else { x.type = "password"; }
    }

    clickOnLogin = event => {
        const { accountEmail, accountPassword, memberRemember } = this.state
        var self = this;
        const { prefix } = this.props
        this.setState({ loginLoader: true })
        var redirectURL = ""
        localStorage.setItem("socialLogin", 0)

        if (accountEmail && String(accountEmail).length > 0 && accountPassword && String(accountPassword).length > 0) {
            if(memberRemember) this.updateMemberData()
            getIPDetails().then((ipResponse) => {
                const { data } = ipResponse
                this.props.loginUser({
                    emailId: accountEmail, password: accountPassword, countryCode: data.country
                }, redirectURL, self.props.history, self.callBackError);
            })
        }
        else {
            if (!accountEmail || String(accountEmail).length == 0) this.setState({ emailError: true })
            if (!accountPassword || String(accountPassword).length == 0) this.setState({ passwordError: true })
            this.setState({
                loginLoader: false
            })
        }
    }

    socialLogin = (socialData) => {
        var { accountFullName, accountEmailId, createFrom, picture, authId, activeProduct } = socialData
        const { prefix, history } = this.props
        var self = this;
        var activeProduct = "0"
        localStorage.setItem("socialLogin", 1)
        if (prefix && String(prefix).includes("widget")) {
            activeProduct = "6"
        }
        else if (prefix && String(prefix).includes("display")) {
            activeProduct = "4"
        }
        getIPDetails().then((ipResponse) => {
            const { data } = ipResponse
            const trackingPage = getCookieValue('tracking_URL')
            const landingPage = getCookieValue('landing_URL')
            this.props.userSignUp({ fullName: accountFullName, emailId: accountEmailId, password: "", trackingPage:trackingPage, landingPage:landingPage, activeProduct: activeProduct, createFrom: createFrom, picture: picture, oauth_uid: authId, countryCode: data && data.country ? data.country : 'US',utm_source: createFrom == 'facebook' ? "facebook_ads" : createFrom }, self.props.history, () => {
            })
        })
    }
    onFormSubmit = e => {
        e.preventDefault();
    }
    callBackError = (message) => this.setState({ loginSuccessError: message, loginLoader: false })
    render() {
        const { prefix } = this.props
        const { accountEmail, accountPassword, emailError, passwordError, loginLoader, loginSuccessError, memberRemember } = this.state;

        return <>

            <div className="loginFormSec formControlMin">
                <div className="loginBlock">
                    <div className="loginInnSec">
                        <div className="loginWithSocials">
                            <div className="loginHead">
                                <h2>Login to Tagshop</h2>
                            </div>

                            
                            <SocialSignup socialLogin={this.socialLogin}/>

                        </div>
                        {loginSuccessError ? <div id="flashFlushMessage" className="alert alert-block alert-danger fade show in flashNotification">
                            <p className="mb-0">{loginSuccessError}	</p>
                        </div> : null}
                        <form className="contact_form mainLogin" onSubmit={this.onFormSubmit}>
                            <div className="form-group labelPosition emailSecion">
                                <label htmlFor="AccountEmail">Email</label>
                                <input name="data[Account][email]" className={`form-control ${emailError ? `form-error` : ``}`} type="text" value={accountEmail} id="AccountEmail" data-factors-input-id="form-0.field-0" data-gtm-form-interact-field-id="0" onChange={this.emailTextChange} />
                                {emailError ? <span className="errorMessage">
                                    Email is required.
                                </span> : null}
                            </div>

                            <div className="form-group labelPosition passwordSecion">
                                <label htmlFor="AccountPassword">Password</label>
                                <div className="password_wrap">
                                    <input name="data[Account][password]" className={`form-control placeholder-no-fix ${passwordError ? `form-error` : ``}`} type="password" id="AccountPassword" data-gtm-form-interact-field-id="1" onChange={this.passwordTextChange} value={accountPassword} />
                                    <button id="show_password_icon" type="button" className="show_password_icon" onClick={this.showPass} data-factors-form-bind="true" data-factors-click-bind="true">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="22" height="18" viewBox="0 0 24 15">
                                            <path id="Path_5232" data-name="Path 5232" d="M21.87,11.5c-.64-1.11-4.16-6.68-10.14-6.5C6.2,5.14,3,10,2.13,11.5a1,1,0,0,0,0,1c.63,1.09,4,6.5,9.89,6.5h.25c5.53-.14,8.74-5,9.6-6.5A1,1,0,0,0,21.87,11.5ZM12.22,17c-4.31.1-7.12-3.59-8-5,1-1.61,3.61-4.9,7.61-5,4.29-.11,7.11,3.59,8,5-1.03,1.61-3.61,4.9-7.61,5Z" transform="translate(-1.996 -4.996)" fill="#d9d9d9"></path>
                                            <path id="blank_path" data-name="Path 5233" d="M12,8.5A3.5,3.5,0,1,0,15.5,12,3.5,3.5,0,0,0,12,8.5Zm0,5A1.5,1.5,0,1,1,13.5,12,1.5,1.5,0,0,1,12,13.5Z" transform="translate(-1.996 -4.996)" fill="#d9d9d9"></path>
                                            <path xmlns="http://www.w3.org/2000/svg" id="filled_path" data-name="Path 5233" d="M12,8.5A3.5,3.5,0,1,0,15.5,12,3.5,3.5,0,0,0,12,8.5Z" transform="translate(-1.996 -4.996)" fill="#545454"></path>
                                        </svg>
                                    </button>
                                </div>
                                {passwordError ? <span className="errorMessage">
                                    Password is required.
                                </span> : null}
                            </div>
                            <div className="form-group remember_check">
                                <label className="loggedinIco checkbox-squre">
                                    <input type="checkbox" name="data[Account][remember]" checked={memberRemember} onChange={e => this.setState({memberRemember: e.target.checked})}  />
                                    <span></span>Remember me
                                </label>
                                <Link className="pull-right" to={`${prefix}accounts/forgotpassword`}>Forgot Password?</Link>
                            </div>
                            <div className="form-group">
                                <button type="submit" className={`btn btn-primary w-100${loginLoader ? ' disabled' : ''}`} onClick={this.clickOnLogin}>
                                    <span className="indicator-label" style={{ display: loginLoader ? 'none' : 'block' }}>Login</span>
                                    <span className="indicator-progress" style={{ display: !loginLoader ? 'none' : 'block' }}> Please wait...
                                        <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                                    </span>
                                </button>
                            </div>
                            <div className="form-group mb-0">
                                <p>Don't have an account? <Link className="btnSignUp" to={`${prefix}accounts/register`}>Sign Up</Link></p>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    }
}
const mapStateToProps = (state) => {
    return { }
}
const mapDispatchToProps = dispatch => {
    return {
        userSignUp: (data, history, responseCallback) => dispatch(userSignUp(data, history, responseCallback)),
        loginUser: (postbody, history, redirectURL, callback) => dispatch(loginUser(postbody, history, redirectURL, callback))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Login);